body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[data-reactroot],
#root {
  height: 90% !important;
  height: calc(100% - 65px) !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body,
.App {
  height: 100%;
  background-color: #242426;
  color: whitesmoke;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  pointer-events: none;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.work-in-progress {
  vertical-align: middle;
  margin: auto;
}

.work-in-progress div {
  display: block;
  text-align: center;
  margin: auto;
}

.work-in-progress img {
  object-fit: scale-down;
  display: block;
  text-align: center;
  margin: auto;
}

.profile-body {
  margin: 0 auto;
  min-width: 360px;
}

.profile-picture {
  margin: 20px auto 10px;
  display: block;
}

.profile-body .profile-picture button[type='button'] {
  margin: 0 auto;
  display: block;
}

.profile-body .profile-picture button[type='button'] img {
  width: 150px !important;
  height: 150px !important;
}

.profile-body .header {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  min-width: 300px;
  background: #343434;
  padding: 2px;
}

.header h3 {
  width: 300px;
  margin: 5px auto;
  text-align: center;
}

.header p {
  text-align: center;
  font-size: 0.8em;
  margin-top: 10px;
  margin-bottom: 10px;
}

.profile-details {
  display: block;
  margin: 0 auto;
  position: relative;
}

.profile-picture {
  padding: 6px;
}

.profile-picture button[type='button'] {
  border: none;
  background: none;
  color: inherit;
  padding: 0;
  cursor: pointer;
}

.profile-picture button[type='button'] img {
  object-fit: cover;
  display: block;
  margin: 0 auto;
  height: 100px;
  width: 100px;
  border-radius: 50%;
  border: 2px solid #909090;
}

#pdf-container {
  height: 100%;
  width: 100%;
}

