.profile-picture {
  padding: 6px;
}

.profile-picture button[type='button'] {
  border: none;
  background: none;
  color: inherit;
  padding: 0;
  cursor: pointer;
}

.profile-picture button[type='button'] img {
  object-fit: cover;
  display: block;
  margin: 0 auto;
  height: 100px;
  width: 100px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  border-radius: 50%;
  border: 2px solid #909090;
}
