.profile-body {
  margin: 0 auto;
  min-width: 360px;
}

.profile-picture {
  margin: 20px auto 10px;
  display: block;
}

.profile-body .profile-picture button[type='button'] {
  margin: 0 auto;
  display: block;
}

.profile-body .profile-picture button[type='button'] img {
  width: 150px !important;
  height: 150px !important;
}

.profile-body .header {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  min-width: 300px;
  background: #343434;
  padding: 2px;
}

.header h3 {
  width: 300px;
  margin: 5px auto;
  text-align: center;
}

.header p {
  text-align: center;
  font-size: 0.8em;
  margin-top: 10px;
  margin-bottom: 10px;
}

.profile-details {
  display: block;
  margin: 0 auto;
  position: relative;
}
